<script>
import { defineComponent, h, ref } from "vue";
import { mixinColor, mixinView } from '../../mixins'
import { addMixinPrefix } from "../../util/helpers";

export default defineComponent({
  name: "TimelineItem",
  mixins: [...addMixinPrefix([mixinColor, mixinView()], "timeline-item")],
  setup(props, { slots }) {
    const views = ref(['filled', 'smooth'])
    const timelineItemChild = () => {
      return [
        slots.datetime && h("div", { class: "timeline__item__datetime" }, slots.datetime()) || '',
        slots.content && h("div", { class: "timeline__item__content" }, slots.content()) || '',
      ];
    };

    return {
      timelineItemChild,
      views
    };
  },
  render() {
    return h("div", {
      class: ["timeline__item", this.classColor, this.classView] ,
    }, this.timelineItemChild());
  },
});
</script>